.main-container {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    min-height: 100vh;
    background-color: rgba(255,255,255,0.1);
    background-image: linear-gradient(pink, black);
    /* font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif; */
}

.login-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100vh;
    width: 100%;
}

.message-container {
    background-color: black;
    color: white;
    padding: 15px; /* Adjust the padding as needed */
    position: relative; /* Add this line */
    border-style:solid;
    border-top: 0px;
    border-left: 0px;
    border-right: 0px;
    border-color: whitesmoke;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
}

.image-container {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    color: whitesmoke;
    /* min-height: 330px; */
}

.img {
    width: 330px;
    margin: 0 auto;
    border-radius: 25px;
    margin-top: 10px;
}

.input-container {
    display: flex;
}

.input_ao_ta {
    height: 50px;
    width: 250px;
    border-radius: 50px;
    text-align: center;
}

.input_ao_btn {
    height: 50px;
    width: 50px;
    border-radius: 50px;
}

/* .help-div{
    position: fixed;
    bottom: 0;
} */

.help_btn {
    position: fixed;
    bottom: 0;
    right: 0;
    margin: 10px;
    font-size: 16px;
    width: 200px;
    height: 50px;
    border-radius: 50px;
    background-color: brown;
    color: white;
}

.spotify-container{
    margin: 10px;
    color: black;
}

.spotify-container:nth-child(2){
    color: whitesmoke;
}

.footer{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content:flex-start;
    height: 70px;
    background-color: black;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}

.footer-icon{
    display: flex;
    /* align-items: center;
    justify-content: center; */
    height: 30px;
    width: 30px;
    margin: 20px 0;
    margin-left: 10px;
}

.greeting-container {
    opacity: 1;
    transition: opacity 1s ease-out;
}

.greeting-container.fade-out {
    opacity: 0;
}